<template>
  <div class="container">
    <div class="note-detail">
      <div class="note-tit">{{note.title}}</div>
      <div class="note-author">项仁亚 <small>{{note.created_at | fromNow}}</small></div>
      <div class="note-content">{{note.content}}</div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  // import moment from 'moment'
  export default {
    data() {
      return {
        note: {}
      }
    },
    created() {
      let postId = this.$route.params.postId
      axios.get('/posts/' + postId).then((res) => {
        this.note = res.data
      })
    }
  }
</script>

<style>
  .note-detail {
    margin-top: 20px;
  }
  
  .note-tit {
    font-size: 18px;
    margin-bottom: 5px;
  }
  
  .note-author {
    margin-bottom: 20px;
  }
  
  .note-author small {
    color: #999;
  }
  
  .note-content {
    text-indent: 2em;
  }
</style>



