<template>
  <div class="container">
    <form class="form-signin" @submit.prevent="onSubmit">
      <h2 class="form-signin-heading">用户登录</h2>
      <label for="inputUserName" class="sr-only" v-model="username">UserName address</label>
      <input type="text" id="inputUserName" class="form-control" placeholder="用户名" required autofocus>
      <label for="inputPassword" class="sr-only">Password</label>
      <input type="password" id="inputPassword" class="form-control" placeholder="密码" required v-model="password">
      <div class="checkbox">
        <label>
              <input type="checkbox" value="remember-me"> 下次自动登录
            </label>
      </div>
      <button class="btn btn-lg btn-primary btn-block" type="submit">登录</button>
    </form>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        username: '',
        password: '',
        errorMsg: ''
      }
    },
    methods: {
      onSubmit() {
        var userObj = {
          name: this.name,
          password: this.password
        }
        axios.post('users/login', userObj).then((res) => {
          localStorage.setItem('token', res.data.token)
          this.$router.push({
            path: '/'
          })
        }).catch((err) => {
          this.errorMsg = err.response.data.message
        })
      }
    }
  }
</script>

<style lang="scss">  
  .form-signin {
    max-width: 330px;
    padding: 15px;
    margin: 0 auto;
  }
  
  .form-signin .form-signin-heading,
  .form-signin .checkbox {
    margin-bottom: 10px;
  }
  
  .form-signin .checkbox {
    font-weight: normal;
  }
  
  .form-signin .form-control {
    position: relative;
    height: auto;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    padding: 10px;
    font-size: 16px;
  }
  
  .form-signin .form-control:focus {
    z-index: 2;
  }
  
  .form-signin input[type="email"] {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  
  .form-signin input[type="password"] {
    margin-bottom: 10px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
</style>



