import moment from 'moment'

moment.locale('zh-cn')

export default {
  'time': function (value, formatString) {
    formatString = formatString || 'YYYY-MM-DD HH:mm'
    return moment(value).format(formatString)
  },
  'fromNow': function (val) {
    if (!val) return ''
    return moment(val.toString()).fromNow()
  }
}



